.portfolio__container {
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}

.portfolio__projectsWrapper {
  min-width: 1024px;
  display: flex;
  flex-wrap: wrap;
}

.portfolio__appContainer {
  background-color: var(--app-bg);
  border: 5px solid black;
  border-radius: 22px;
  margin: 10px;
  width: 480px;
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.portfolio__appContainer a {
  height: 395px;
}

.portfolio__appLinks {
  border-top: 5px solid black;
  width: 100%;
  min-height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.portfolio__projectImage {
  border: 5px solid yellow;
  object-fit: cover;
  object-position: top;
  width: 470px;
  height: 385px;
}

.portfolio__appTitle {
  padding-top: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: black;
  text-decoration: none;
}

.portfolio__appSubtitle {
  color: rgb(30, 30, 30);
  text-decoration: none;
}

.portfolio__gitLink {
  padding-top: 10px;
  color: blue;
  text-decoration: none;
  height: auto;
}

@media screen and (max-width: 1024px) {
  .portfolio__container {
    width: 100vw;
  }
  
  .portfolio__projectsWrapper {
    min-width: auto;
    flex-direction: column;
  }

  .portfolio__appContainer a {
    height: 295px;
  }
}

@media screen and (max-width: 768px) {
  .portfolio__container {
    width: 100vw;
  }

  .portfolio__appContainer {
    width: 380px;
    height: 400px;
  }

  .portfolio__projectImage {
    width: 370px;
    height: 285px;
  }

  .portfolio__appSubtitle {
    font-size: 16px;
  }
}

@media screen and (max-width: 425px) {
  .portfolio__appContainer {
    margin: 0;
    padding: 0;
    width: 335px;
    height: 400px;
  }

  .portfolio__appContainer {
    margin: 10px 0;
    padding: 0;
    width: 335px;
    height: 400px;
  }

  .portfolio__projectImage {
    margin: 0;
    padding: 0;
    width: 323px;
    height: 285px;
  }

  .portfolio__appSubtitle {
    font-size: 14px;
  }
}
